<template>
  <div>        
    <pulse-loader class="spinner" v-if="isInvoicePublishing" :loading="isInvoicePublishing"></pulse-loader>
    <CRow v-if="isInvoicePublishedError">
      <CCol md="12">
        <CAlert show color="danger">Danger Alert</CAlert>
      </CCol>
    </CRow>
    <CRow>      
      <CCol md="8">        
        <CCard>
          <CCardHeader>
            Documento : {{getDocumentReference.CNPJ}}
          </CCardHeader>
          <CCardBody v-if="!isInvoicePublishing">
            <CRow class="form-group">
                <CCol :col="2">
                    <CInput class="mb-0" label="CNPJ" v-model="getDocumentReference.CNPJ"/>
                </CCol>            
                <CCol :col="2">
                    <CInput class="mb-0" label="Data" v-model="getDocumentReference.Date"/>
                </CCol>            
            </CRow>
            <CRow
              class="form-group"
              v-for="(produto, key) in getDocumentReference.Produtos"
              :key="key"
            >
              <CCol :col="2">
                <CInput class="mb-0" label="EAN" v-model="produto.EAN"/>
              </CCol>      
              <CCol :col="4">
                <CInput class="mb-0" label="Descricao" v-model="produto.Descricao"/>
              </CCol>                                  
              <CCol :col="1">
                <CInput class="mb-0" label="Quantidade" v-model="produto.Quantidade"/>
              </CCol>      
              <CCol :col="1">
                <CInput class="mb-0" label="Unidade" v-model="produto.Unidade"/>
              </CCol>      
              <CCol :col="1">
                <CInput class="mb-0" label="Valor Unidade" v-model="produto.ValorUnidade"/>
              </CCol>                    
              <CCol :col="1">
                <CInput class="mb-0" label="Valor Total" v-model="produto.ValorTotal"/>
              </CCol>      
            </CRow>
          </CCardBody>
          <CCardFooter>
            <CButton color="primary" v-on:click="publish()">Publish</CButton>            
          </CCardFooter>
        </CCard>
      </CCol>
      <CCol md="4">
        <CCard>
          <CCardHeader>
            Cupom fiscal
            <div class="card-header-actions">
              <a 
                href="https://coreui.io/vue/docs/components/card-components" 
                class="card-header-action" 
                rel="noreferrer noopener" 
                target="_blank"
              >
                <small class="text-muted">docs</small>
              </a>
            </div>
          </CCardHeader>
          <CCardBody>
            <img :src="getUrl">
          </CCardBody>
        </CCard>
        
      </CCol>      
    </CRow>    
  </div>
</template>

<script>
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'
import CTableWrapper from './base/Table.vue'

export default {
  name: 'InvoiceCorrection',
  components: { CTableWrapper, PulseLoader },
  computed: {
      getDocumentReference() { return this.$store.state.invoice.selectedInvoice },
      getUrl() { return  'https://kobus.blob.core.windows.net/invoices/' + this.$store.state.invoice.selectedInvoice.TransactionId },
      isInvoicePublishing() { return this.$store.state.invoice.isInvoicePublishing},
      isInvoicePublishedError() { return this.$store.state.invoice.isInvoicePublishedError},
  },
  mounted: function() {
    
  },
  methods: {        
    publish() {      
      this.$store.dispatch('invoice/postInvoice', this.getDocumentReference)

    }
  }
}
</script>
<style>
.spinner {
  display: block;
  position: fixed;
  z-index: 1031; /* High z-index so it is on top of the page */
  top: 50%;
  right: 50%; /* or: left: 50%; */
  margin-top: -..px; /* half of the elements height */
  margin-right: -..px; /* half of the elements widht */
}
</style>